* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body , #root {
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: 'Prompt', sans-serif;
  color: #FFF;
}

a:visited{
  color : red;
}
h1{
  font-size: 0.8em;
}
h2{
  font-size: 0.8em;
}
span , p {
  font-size: 0.6em;
}

.table{
  height: 100%;
  min-height: 710px;
  overflow: hidden;
}

.github{
  display: none;
}

.paper.page1{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  height: 100%;
  width: 100%;
  z-index: 1;
}

.header-profile{
  position: relative;
  width: 100%;
  height: 20%;
  min-height: 120px;
  background: #484848;
  padding: 2.5% 5%;
}
.header-profile h1{
  display: inline;
  margin-right: 10px;
  color:aqua;
}

.flex{
  display: -ms-flexbox;
  display: flex;
}
.flex-item{
  width: 50%;
}

.white-text{
  color: white;
}

.body-profile{
  position: relative;
  width: 100%;
  height: calc(100% - 20%);
}
.body-profile h2{
    
  margin: 0;
}
.about{
  position: relative;
  -ms-flex: 4 1;
      flex: 4 1;
  min-height: 380px;
  padding: 1% 5%;
  background: #888;
}


.skill {
  position: relative;
  -ms-flex: 6 1;
      flex: 6 1;
  min-height: 380px;
  padding: 1% 5%; 
  background: #aaa;
}

span.checked{
  color:rgb(255, 230, 0)
}
@media screen and (min-width: 360px){
  h1{
    font-size: 1.1em;
  }
  h2{
    font-size: 0.9em;
  }
  span , p {
    font-size: 0.8em;
  }
}
@media screen and (min-width: 550px) {
  h1{
    font-size: 1.5em;
  }
  h2{
    font-size: 1.5em;
  }
  span , p {
    font-size: 1em;
  }
  .github{
    display: block;
    position: absolute;
    right: 5px;;
    top:5px;
    width: 8%;
    min-width: 60px;
    z-index: 2;
  }
  
  .github img{
    height: 100%;
    width: 100%; 
  }
}
@media screen and (min-width: 768px) {
  h1{
    font-size: 22px;
  }
  h2 {
    font-size: 18px;
  }
  span , p {
    font-size: 14px;
  }
  .table{
    height: 100%;
    background-image: url("./images/table.jpeg");
    background-size: 50%;
  }
  .post-it {
    position: relative;
    width: 240px;
    height: 200px;
    margin: 0;
    -webkit-transform: rotate(-10deg);
        -ms-transform: rotate(-10deg);
            transform: rotate(-10deg);
    background: #e0ec74;
    background: -webkit-gradient(linear, left top, left bottom, from(#e0ec74), to(#efffc7));
    background: -webkit-linear-gradient(top, #e0ec74, #efffc7);
    background: -o-linear-gradient(top, #e0ec74, #efffc7);
    background: linear-gradient(to bottom, #e0ec74, #efffc7);
    -webkit-box-shadow: 20px 15px 20px rgba(0,0,0,.7);
            box-shadow: 20px 15px 20px rgba(0,0,0,.7);
    cursor: pointer;
    z-index: 2;
  }
  .post-it:hover{
    background: #e0ec74;
    background: -webkit-gradient(linear, left top, left bottom, from(#e2f535), to(#c8d89a));
    background: -webkit-linear-gradient(top, #e2f535, #c8d89a);
    background: -o-linear-gradient(top, #e2f535, #c8d89a);
    background: linear-gradient(to bottom, #e2f535, #c8d89a);
    -webkit-box-shadow: 25px 25px 40px rgba(0,0,0,.7);
            box-shadow: 25px 25px 40px rgba(0,0,0,.7);
  }
  .postit-text{
    font-size: 30px;
  }
  .note {
    width: 100%;
    font-family: 'Itim', cursive;
    text-align: center;
    color: rgb(68, 68, 241);
    position: relative;
    top:25%;
    
  }
  .pin {
    background-color: #aaa;
    display: block;
    height: 32px;
    width: 2px;
    position: absolute;
    left: 50%;
    top: -16px;
    z-index: 3;
  }
  .pin:after {
    background-color: #A31;
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(25%, 25%), color-stop(circle), color-stop(hsla(0,0%,100%,.1)), to(hsla(0,0%,0%,.3)));
    background-image: -webkit-linear-gradient(25% 25%, circle, hsla(0,0%,100%,.1), hsla(0,0%,0%,.3));
    background-image: -o-linear-gradient(25% 25%, circle, hsla(0,0%,100%,.1), hsla(0,0%,0%,.3));
    background-image: linear-gradient(25% 25%, circle, hsla(0,0%,100%,.1), hsla(0,0%,0%,.3));
    border-radius: 50%;
    -webkit-box-shadow: inset 0 0 0 1px hsla(0,0%,0%,.1),
                inset 3px 3px 3px hsla(0,0%,100%,.5),
                inset -3px -3px 3px hsla(0,0%,0%,.6),
                23px 20px 5px hsla(0,0%,0%,.35);
            box-shadow: inset 0 0 0 1px hsla(0,0%,0%,.1),
                inset 3px 3px 3px hsla(0,0%,100%,.5),
                inset -3px -3px 3px hsla(0,0%,0%,.6),
                23px 20px 5px hsla(0,0%,0%,.35);
    content: '';
    height: 12px;
    left: -5px;
    position: absolute;
    top: -10px;
    width: 12px;
  }
  .pin:before {
    background-color: hsla(0,0%,0%,0.3);
    -webkit-box-shadow: 0 0 .5em hsla(0,0%,0%,0.2);
            box-shadow: 0 0 .5em hsla(0,0%,0%,0.2);
    content: '';
    height: 24px;
    width: 2px;
    left: 0;
    position: absolute;
    top: 8px;
    -webkit-transform: rotate(57.5deg);
        -ms-transform: rotate(57.5deg);
            transform: rotate(57.5deg);
    -webkit-transform-origin: 50% 100%;
        -ms-transform-origin: 50% 100%;
            transform-origin: 50% 100%;
 
  }
  .paper , .paper.page1{
    position: absolute;
    height: 85%;
    min-height: 580px;
    width: 65%;
    max-width: 675px;
    left:25%;
    top: 55px;
    background: #F5F5F5;
    border-radius: 2px;
    -webkit-transform: rotate(3deg);
        -ms-transform: rotate(3deg);
            transform: rotate(3deg);
    -webkit-box-shadow: 12px 12px 120px rgba(0, 0, 0, .3);
            box-shadow: 12px 12px 120px rgba(0, 0, 0, .3);
  }
  .header-profile{
    position: relative;
    height: 20%;
    min-height: 120px;
    background: #484848;
    padding: 2.5% 5%;
  }
  .github{
    background: coral;
    border: 2px solid tomato;
    border-bottom: 1px solid tomato;
    border-top: 1px solid tomato;
    border-radius: 50%;
  }
  .github img{
    -webkit-animation-name: spin;
            animation-name: spin;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear; 
  }
}

@media screen and (min-width: 1025px) {
  h1{
    font-size: 24px;
  }
  h2 {
    font-size: 20px;
  }
  span , p {
    font-size: 16px;
  }
  .post-it{
    top: 4%;
    left: 2.5%;
    width: 300px;
    height: 250px; 
  }
  .github{
    right: 5%;;
    top: 5%;
    width: 8%;
    min-width: 60px;
    z-index: 2;
  }
  .paper , .paper.page1{
    left:30%;
    -webkit-box-shadow: 12px 12px 140px rgba(0, 0, 0, .3);
            box-shadow: 12px 12px 140px rgba(0, 0, 0, .3);
  }
  .postit-text{
    font-size: 40px;
  }
}

@-webkit-keyframes spin {
  0%   {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  25%  {-webkit-transform:rotate(25deg);transform:rotate(25deg);}
  50%  {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  75%  {-webkit-transform:rotate(-25deg);transform:rotate(-25deg);}
  100% {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
}

@keyframes spin {
  0%   {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  25%  {-webkit-transform:rotate(25deg);transform:rotate(25deg);}
  50%  {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  75%  {-webkit-transform:rotate(-25deg);transform:rotate(-25deg);}
  100% {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
}



